.zodiacdesc-root {
    width: 1920px;
    height: 1080px;
    .list {
        position: fixed;
        top: 72px;
        left: 0;
        display: flex;
        flex-flow: column;
        align-items: flex-start;
        z-index: 1;
        .li {
            color: #A69590;
            font-size: 23px;
            font-family: SimSun;
            cursor: pointer;
            line-height: 77px;
            margin-left: 46px;
            margin-right: 84px;
            padding: 0 30px;
        }
        .selected {
            font-size: 28px;
            font-weight: bold;
            color: #E98C6F;
            border: 2px solid #F59170;
            border-radius: 38px;
        }
        .selected-line {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            width: 46px;
            height: 2px;
            background: #F59170;
        }
        .selected-icon {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    .day {
        position: fixed;
        top: 750px;
        left: 1380px;
        font-family: SimSun;
        font-size: 60px;
        color: #C0A49B;
    }
    .text {
        position: fixed;
        left: 0;
        top: 912.5px;
        width: 1920px;
        height: 1080px;
        img {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
    }
}
@primary-color: #389e0d;@link-color: #389e0d;@font-size-base: 15px;@layout-header-padding: 0px;@text-color: fade(#000, 95%);@border-color-base: #888888;@border-color-split: #cccccc;@btn-height-lg: 38px;@input-height-lg: 38px;