.calendar-root {
    position: absolute;
    top: 0;
    left: 0;
    width: 1920px;
    height: 1080px;
    background: rgba(18, 22, 33, 0.75);
    z-index: 100;
    .content-box-bg {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: auto;
        width: 960px;
        height: 660px;
    }
    .content-box {
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        margin: auto;
        width: 960px;
        height: 660px;
        .bg {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
        .header {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100px;
            color: #fff;
            font-size: 33px;
            font-weight: bold;
            background: radial-gradient(circle, rgba(165, 111, 121, 0.5) 1%, rgba(91, 67, 80, 0.5) 61%, rgba(66, 53, 70, 0.5) 100%);
            display: flex;
            align-items: center;
            justify-content: space-between;
         }
         .center {
            position: absolute;
            background: rgba(18, 22, 33, 0.6);
            width: 100%;
            height: 493px;
            left: 0;
            top: 100px;
            .day {
                position: absolute;
                width: 80%;
                left: 50%;
                transform: translateX(-50%);
                top: 50px;
                color: #fff;
                font-size: 30px;
                display: flex;
                flex-wrap: wrap;
            }
            .dayitem {
                position: relative;
                height: 80px;
                width: 107px;
                cursor: pointer;
                .dayitem-img {
                   width: 114px;
                   position: absolute;
                   top: -17px;
                   left: -3px;
               }
               .dayitem-text {
                   width: 100%;
                   height: 100%;
                   position: absolute;
                   top: 0;
                   left: 0;
                   line-height: 80px;
                   text-align: center;
                   color: #fff;
                   opacity: 0.2;
               }
               .selected {
                   opacity: 1;
               }
            }
         }
         .footer {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            line-height: 67px;
            color: #fff;
            text-align: center;
            font-size: 32px;
            background: radial-gradient(circle, rgba(88, 71, 86, 0.6) 0%, rgba(60, 52, 73, 0.6) 70%);
            cursor: pointer;
        }
    }
}
@primary-color: #389e0d;@link-color: #389e0d;@font-size-base: 15px;@layout-header-padding: 0px;@text-color: fade(#000, 95%);@border-color-base: #888888;@border-color-split: #cccccc;@btn-height-lg: 38px;@input-height-lg: 38px;